import './index.scss'
import Head from '~/components/Head'
import {axios} from '&/axios';

export default class BookStoreInfo extends Component {
  constructor() {
    super();
    this.state = {
      img: require('@/assets/images/store.jpg'),
      text:''
    }
  }


  componentDidMount(){
    axios.get('/bookstore/getMsg').then(res=>{
      this.setState({
        text:res.data.text
      })
    })
  }

  render() {
    return (
      <div className='storeinfo'>
        <Head title='简介' showBack={true}></Head>
        <div className='infoBox'>
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <img src={this.state.img} style={{ width: '95%', height: '200px' }} alt="" />
          </div>
          <div style={{ padding: '5px', lineHeight: '24px' }}>
            <p style={{ textIndent: '24px' }}>{this.state.text}</p>
          </div>
        </div>
      </div>
    )
  }
}
